.page {
  background-color: black;
  min-height: 100vh;
  max-height: auto;
}

#textarea1 {
  color: white;
}

.add-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.show-item {
  margin-top: 5%;
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-wrap: wrap;
}

.body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 5%;
}

.card-content {
  width: 400px;
  font-weight: bold;
  font-size: 25px;
  color: white;
  text-align: center;
}
